<template>
	<view class="login">
		<view class="bg_img">
			<image style="height:630px;width:100%;" :src="img" mode=""></image>
		</view>
		<view>
			<img :src="src == '' ? '' : src" class="user_img" @click="src_img" />
		</view>
		<view class="reg-info">
			<input type="text" placeholder="用户名/手机号" />
			<input type="password" placeholder="密码" />
			<view class="forget_pwd">
				<span class="forget_pwd_1">忘记密码?</span>
				<span class="forget_pwd_2" @click="res_ZH">注册账号</span>
			</view>
			<view class="btn_login">
				<image style="height:45px;width:60%;margin:0 auto;display: block;" :src="button_img"></image>
				<span class="user_login">登录</span>
			</view>
		</view>
		<view class="other-login">
			<p>---更多登陆选项---</p>
			<view class="other-img">
				<img class="other-login_1" src="/static/image/qq.png" @click="qq_login">
				<img class="other-login_2" src="/static/image/weixin.png" @click="wx_login">
			</view>
		</view>
	</view>
</template>

<script>
	var _self;
	export default {
		data() {
			return {
				src: '/static/image/qq.png',
				img: '/static/image/bg.png',
				button_img: '/static/image/button.png'
			};
		},
		methods: {
			src_img() {
				_self = this;
				uni.chooseImage({
					count: 1,
					sourceType: ['album', 'camera'],
					success(res) {
						const tempFilePaths = res.tempFilePaths;
						this.src = tempFilePaths['0'];

						uni.uploadFile({
							url: 'http://10.2.48.2/API/upLoadImg/UpLoadImg.php',
							filePath: tempFilePaths[0],
							name: 'file',
							success: function(res) {
								var data = res.data;
								console.log(data);
							},
							fail: function() {
								console.log('fail')
							}
						})
					}
				})
			},
			qq_login() {
				_self = this;
				uni.login({
					provider: 'qq',
					success(loginRes) {
						console.log(loginRes.authResult);
						uni.getUserInfo({
							provider: 'qq',
							success: function(infoRes) {
								//用户名
								console.log(infoRes.userInfo.nickName);
								//头像
								_self.src = infoRes.userInfo.avatarUrl;
								//openId保存本地缓存

								uni.setStorageSync('openId', infoRes.userInfo.openId);
								// 								uni.navigateTo({
								// 									url: 'voice',
								// 								});
							}
						});
					}
				})
			},
			wx_login() {
				_self = this;
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						console.log(loginRes.authResult);
						// 获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								console.log('用户昵称为：' + infoRes.userInfo.nickName);
								_self.src = infoRes.userInfo.avatarUrl;
							}
						});
					}
				});
			},
			res_ZH() {
				uni.navigateTo({
					url:"../login/login"
				})
			}
		}
	}
</script>

<style>
	.bg_img {
		justify-content: center;
	}

	.user_img {
		width: 150upx;
		height: 150upx;
		border-radius: 50%;
		background: #8F8F94;
		position: fixed;
		top: 270upx;
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
	}

	.reg-info {
		position: fixed;
		top: 440upx;
		width: 100%;
	}

	input {
		border-bottom: 1px solid gray;
		width: 75%;
		margin: 0 auto;
		margin-top: 70upx;
		font-size: 30upx;
	}

	.forget_pwd {
		width: 75%;
		margin: 0 auto;
		font-size: 35upx;
		margin-top: 10px;
	}

	.forget_pwd_1 {
		float: left;
		color: red;
	}

	.forget_pwd_2 {
		float: right;
	}

	.btn_login {
		position: fixed;
		top: 840upx;
		width: 100%;
		margin: 0 auto;
	}

	.user_login {
		border: none;
		color: black;
		font-size: 30upx;
		background-size: cover;
		display: block;
		text-align: center;
		margin-top: -34px;
		font-size: 35upx;
		color: white;
	}

	.other-login {
		position: fixed;
		top: 1050upx;
		width: 100%;
		height: 300upx;
	}

	.other-login p {
		font-size: 25upx;
		text-align: center;
	}

	.other-img {
		justify-content: center;
		margin: 0 auto;
		display: flex;
		margin-top: 60upx;
	}

	.other-login_1 {
		width: 70px;
		height: 70px;
	}

	.other-login_2 {
		width: 70px;
		height: 70px;
		margin-left: 60upx;
	}
</style>



